export default "@import '../../themes/src/default.css';\n@import '../../tag/src/vars.css';\n\n:root {\n    --filter-tag-separator-background-color-hover: transparent;\n}\n\n.component {\n    display: flex;\n    width: max-content;\n    box-sizing: border-box;\n\n    &.disabled {\n        background-color: var(--tag-outlined-background-color-disabled);\n    }\n\n    &.rounded {\n        &.xxs {\n            border-radius: var(--tag-rounded-xxs-border-radius);\n        }\n\n        &.xs {\n            border-radius: var(--tag-rounded-xs-border-radius);\n        }\n\n        &.s {\n            border-radius: var(--tag-rounded-s-border-radius);\n        }\n    }\n\n    &.rectangular {\n        &.xxs {\n            border-radius: var(--tag-rectangular-xxs-border-radius);\n        }\n\n        &.xs {\n            border-radius: var(--tag-rectangular-xs-border-radius);\n        }\n\n        &.s {\n            border-radius: var(--tag-rectangular-s-border-radius);\n        }\n    }\n\n    &.checked {\n        &:not(.disabled) {\n            background-color: var(--tag-background-color-checked);\n        }\n\n        &:not(.disabled).xxs {\n            min-width: 84px;\n        }\n\n        &:not(.disabled).xs {\n            min-width: 104px;\n        }\n\n        &:not(.disabled).s {\n            min-width: 116px;\n        }\n\n        &:not(.disabled):hover {\n            background-color: var(--tag-background-color-checked-hover);\n        }\n\n        &:not(.disabled):active {\n            background-color: var(--tag-background-color-checked-active);\n        }\n    }\n}\n\n.focused {\n    @mixin focus-outline;\n}\n\n.chevron {\n    position: relative;\n    transition: transform 0.2s ease;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color: var(--color-light-graphic-primary);\n}\n\n.valueButton {\n    display: flex;\n    align-items: center;\n    box-shadow: none;\n    user-select: none;\n    cursor: pointer;\n    outline: none;\n    white-space: nowrap;\n    font-family: var(--font-family);\n    color: var(--tag-text-color);\n    background-color: var(--tag-outlined-background-color);\n    border-width: var(--tag-border-width);\n    border-color: var(--tag-border-color);\n    border-style: solid;\n    transition: border-color 0.2s ease, background-color 0.2s ease, color 0.2s ease;\n    margin: 0;\n\n    &:disabled {\n        cursor: var(--disabled-cursor);\n\n        & > .chevron {\n            color: var(--color-light-graphic-primary-alpha-30);\n        }\n    }\n\n    &.rounded {\n        &.xxs {\n            border-radius: var(--tag-rounded-xxs-border-radius);\n        }\n\n        &.xs {\n            border-radius: var(--tag-rounded-xs-border-radius);\n        }\n\n        &.s {\n            border-radius: var(--tag-rounded-s-border-radius);\n        }\n    }\n\n    &.rectangular {\n        &.xxs {\n            border-radius: var(--tag-rectangular-xxs-border-radius);\n        }\n\n        &.xs {\n            border-radius: var(--tag-rectangular-xs-border-radius);\n        }\n\n        &.s {\n            border-radius: var(--tag-rectangular-s-border-radius);\n        }\n    }\n\n    &.xxs {\n        min-width: 60px;\n        min-height: var(--size-xxs-height);\n\n        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n            /* Хак для ие, в котором не работает центрирование по высоте с использованием флексов, если указан min-height\n            Возможно другое решение - если в будущем у компонента появится обертка, установить ей display: flex / inline-flex */\n            height: 32px;\n        }\n    }\n\n    &.xs {\n        min-width: 72px;\n        min-height: var(--size-xs-height);\n\n        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n            /* Хак для ие, в котором не работает центрирование по высоте с использованием флексов, если указан min-height\n            Возможно другое решение - если в будущем у компонента появится обертка, установить ей display: flex / inline-flex */\n            height: 40px;\n        }\n    }\n\n    &.s {\n        min-width: 80px;\n        min-height: var(--size-s-height);\n\n        @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n            /* Хак для ие, в котором не работает центрирование по высоте с использованием флексов, если указан min-height\n            Возможно другое решение - если в будущем у компонента появится обертка, установить ей display: flex / inline-flex */\n            height: 48px;\n        }\n    }\n\n    &.outlined {\n        color: var(--tag-text-color);\n        border-color: var(--tag-border-color);\n        background-color: var(--tag-outlined-background-color);\n\n        &:disabled {\n            border-color: var(--tag-border-color-disabled);\n            color: var(--tag-text-color-disabled);\n            background-color: var(--tag-outlined-background-color-disabled);\n        }\n\n        &:not(:disabled):hover {\n            border-color: var(--tag-border-color-hover);\n            background-color: var(--tag-outlined-background-color-hover);\n        }\n\n        &:not(:disabled):active {\n            border-color: var(--tag-border-color-active);\n            background-color: var(--tag-outlined-background-color-active);\n        }\n    }\n\n    &.filled {\n        color: var(--tag-text-color);\n        border-color: var(--tag-filled-border-color);\n        background-color: var(--tag-filled-background-color);\n\n        &:disabled {\n            color: var(--tag-text-color-disabled);\n            background-color: var(--tag-filled-background-color-disabled);\n        }\n\n        &:not(:disabled):hover {\n            background-color: var(--tag-filled-background-color-hover);\n        }\n\n        &:not(:disabled):active {\n            background-color: var(--tag-filled-background-color-active);\n        }\n    }\n\n    &.s {\n        @mixin paragraph_primary_medium;\n        padding-right: var(--gap-s);\n        padding-left: var(--gap-xl);\n\n        & .chevron {\n            margin: 0 var(--gap-xs);\n        }\n    }\n\n    &.xs {\n        @mixin paragraph_primary_small;\n        padding-right: var(--gap-xs);\n        padding-left: var(--gap-l);\n\n        & .chevron {\n            margin: 0 var(--gap-xs) 0 var(--gap-2xs);\n        }\n    }\n\n    &.xxs {\n        @mixin paragraph_primary_small;\n        padding-right: var(--gap-2xs);\n        padding-left: var(--gap-m);\n\n        & .chevron {\n            margin: 0 var(--gap-xs) 0 var(--gap-2xs);\n        }\n    }\n\n    &.open .chevron {\n        transform: rotateZ(180deg);\n    }\n\n    &.checked {\n        background-color: var(--tag-background-color-checked);\n        border-width: 0;\n        color: var(--tag-text-color-checked);\n\n        & > .chevron {\n            color: var(--color-light-graphic-primary-inverted);\n        }\n\n        &.rounded {\n            &.xxs {\n                border-radius: var(--tag-rounded-xxs-border-radius) 0 0\n                    var(--tag-rounded-xxs-border-radius);\n            }\n\n            &.xs {\n                border-radius: var(--tag-rounded-xs-border-radius) 0 0\n                    var(--tag-rounded-xs-border-radius);\n            }\n\n            &.s {\n                border-radius: var(--tag-rounded-s-border-radius) 0 0\n                    var(--tag-rounded-s-border-radius);\n            }\n\n            &:disabled,\n            &.close {\n                border-radius: var(--tag-border-radius);\n            }\n        }\n\n        &.rectangular {\n            &.xxs {\n                border-radius: var(--tag-rectangular-xxs-border-radius) 0 0\n                    var(--tag-rectangular-xxs-border-radius);\n            }\n\n            &.xs {\n                border-radius: var(--tag-rectangular-xs-border-radius) 0 0\n                    var(--tag-rectangular-xs-border-radius);\n            }\n\n            &.s {\n                border-radius: var(--tag-rectangular-s-border-radius) 0 0\n                    var(--tag-rectangular-s-border-radius);\n            }\n\n            &:disabled,\n            &.close {\n                &.xxs {\n                    border-radius: var(--tag-rectangular-xxs-border-radius);\n                }\n\n                &.xs {\n                    border-radius: var(--tag-rectangular-xs-border-radius);\n                }\n\n                &.s {\n                    border-radius: var(--tag-rectangular-s-border-radius);\n                }\n            }\n        }\n\n        &:not(:disabled) {\n            padding-right: 0;\n            border-right-width: 0;\n        }\n\n        &:not(:disabled):hover {\n            background-color: var(--tag-background-color-checked-hover);\n        }\n\n        &:not(:disabled):active {\n            background-color: var(--tag-background-color-checked-active);\n        }\n\n        &:not(:disabled):hover + .clear:before {\n            background-color: var(--filter-tag-separator-background-color-hover);\n        }\n\n        &:not(:disabled):active + .clear:before {\n            background-color: transparent;\n        }\n\n        &:disabled {\n            background-color: var(--tag-background-color-checked-disabled);\n            color: var(--tag-text-color-checked-disabled);\n        }\n    }\n}\n\n.clear {\n    position: relative;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    color: var(--color-light-graphic-tertiary);\n    background-color: var(--tag-background-color-checked);\n\n    &.rounded {\n        &.xxs {\n            border-radius: 0 var(--tag-rounded-xxs-border-radius)\n                var(--tag-rounded-xxs-border-radius) 0;\n        }\n\n        &.xs {\n            border-radius: 0 var(--tag-rounded-xs-border-radius) var(--tag-rounded-xs-border-radius)\n                0;\n        }\n\n        &.s {\n            border-radius: 0 var(--tag-rounded-s-border-radius) var(--tag-rounded-s-border-radius) 0;\n        }\n    }\n\n    &.rectangular {\n        &.xxs {\n            border-radius: 0 var(--tag-rectangular-xxs-border-radius)\n                var(--tag-rectangular-xxs-border-radius) 0;\n        }\n\n        &.xs {\n            border-radius: 0 var(--tag-rectangular-xs-border-radius)\n                var(--tag-rectangular-xs-border-radius) 0;\n        }\n\n        &.s {\n            border-radius: 0 var(--tag-rectangular-s-border-radius)\n                var(--tag-rectangular-s-border-radius) 0;\n        }\n    }\n\n    &:before {\n        content: '';\n        position: absolute;\n        left: 0;\n        top: 50%;\n        transform: translateY(-50%);\n        display: block;\n        background-color: var(--color-light-border-underline-inverted);\n        width: 1px;\n        transition: background-color 0.2s ease;\n    }\n\n    &:hover:before {\n        background-color: var(--filter-tag-separator-background-color-hover);\n    }\n\n    &:active:before {\n        background-color: transparent;\n    }\n\n    &:hover {\n        background-color: var(--tag-background-color-checked-hover);\n        color: var(--color-light-graphic-primary-inverted);\n    }\n\n    &:active {\n        background-color: var(--tag-background-color-checked-active);\n        color: var(--color-light-graphic-primary-inverted);\n    }\n\n    &.s {\n        padding: 0 var(--gap-s) 0 var(--gap-xs);\n\n        &:before {\n            height: 24px;\n        }\n    }\n\n    &.xs {\n        padding: 0 var(--gap-xs);\n\n        &:before {\n            height: 24px;\n        }\n    }\n\n    &.xxs {\n        padding: 0 3px 0 var(--gap-2xs);\n\n        &:before {\n            height: 20px;\n        }\n    }\n}\n\n.iconWrapper {\n    width: 24px;\n    height: 24px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n";